import { Component, ContentChildren, QueryList, AfterContentInit, ViewChild, ElementRef, AfterViewInit, Input } from '@angular/core';
import { DraggableComponent } from '../draggable/draggable.component';
import SmoothDnD from 'smooth-dnd';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'ngx-smooth-dnd-container',
  template: `<div #container>
  <div>Draggable 1</div>
  <div>Draggable 2</div>
  <div>Draggable 3</div>
  <!-- <ng-content></ng-content> -->
</div>`
})
export class ContainerComponent implements AfterViewInit {
  @ContentChildren(DraggableComponent) tabs: QueryList<DraggableComponent>;
  @ViewChild('container') containerElementRef: ElementRef;
  @Input('options') options;
  private container: any;
  // contentChildren are set
  ngAfterViewInit() {
    this.container = SmoothDnD(this.containerElementRef.nativeElement, this.options);
  }
}
